<template>
  <div id="contact_page" class="contact_page">
    <div id="title_part" class="title_part">
      <div id="title" class="title">{{ global.lang_pack.contact_us }}</div>
      <div id="title_line" class="title_line"></div>
    </div>

    <div class="contact_part">
      <div class="big_text_part">
        <div class="big_text">{{ global.lang_pack.let_connect }}</div>
        <div class="connect_email">{{ global.lang_pack.connect_email }}</div>
      </div>

      <div  class="right_part">
        <div  class="right_title">{{ global.lang_pack.hongkong }}</div>
        <div  class="right_content">{{ global.lang_pack.hongkong_address }}</div>
        <div  class="right_title shenzhen_title">{{ global.lang_pack.shenzhen }}</div>
        <div  class="right_content">{{ global.lang_pack.shenzhen_address }}</div>
      </div>
    </div>

    <div id="contact_part" class="contact_part hire_part">
      <div class="big_text_part">
        <div id="big_text" class="big_text">{{ global.lang_pack.we_are_hiring }}</div>
      </div>
      <div  class="right_part">
        <div  class="right_content">{{ global.lang_pack.hire_intro }}</div>
        <a  class="apply_title" href="mailto:info@signcheckhk.com">{{ global.lang_pack.apply }}</a>
        <div  class="apply_line"></div>
      </div>
    </div>

    <img id="finger" class="finger" src="/static/contact/finger.png"/>

    <Footer></Footer>
    <LangTransfer></LangTransfer>
    <Menu></Menu>
  </div>
</template>

<style lang="css" scoped>
div {
  font-family: HelveticaNeueLTProLtEx;
  color: #37383d;
}

::-webkit-scrollbar {
  display: none;
}

div.contact_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

div.title_part {
  margin-top: 100rem;
  margin-left: 300rem;
}

div.title {
  color: #251714;
  font-family: HelveticaNeueLTProEx;
  font-size: 100rem;
  text-transform: uppercase;
}

div.title_line {
  /* position: absolute; */
  background: #efcd24;
  width: 500rem;
  height: 8rem;
  left: 625rem;
}

div.contact_part {
  margin-top: 100rem;
  display: flex;
  flex-direction: row;
  padding-left: 300rem;
  padding-right: 300rem;
  align-items: center;
}

div.hire_part {
  margin-top: 200rem;
}

div.big_text_part {
  /*padding-left: 100rem;*/
  padding-right: 300rem;
  width: 600rem;
  display: flex;
  flex-direction: column;
}

div.big_text {
  color: #251714;
  font-family: HelveticaNeueLTProEx;
  font-size: 100rem;
  line-height: 1;
  text-transform: capitalize;
  white-space: pre-line;

}

div.connect_email {
  padding-top: 30rem;
  font-size: 45rem;
  letter-spacing: 0.5px;
  /* width: 100rem; */
}

div.right_part {
  display: flex;
  flex-direction: column;
}

div.right_title {
  color: #251714;
  font-family: HelveticaNeueLTProEx;
  font-size: 50rem;
  line-height: 1;
}

div.right_content {
  width: 500rem;
  font-size: 18px;
  margin-top: 20rem;
  letter-spacing: 0.5px;
  line-height: 2;
  white-space: pre-line;
  text-align: justify;
  text-justify: distribute-all-lines;
}

div.shenzhen_title {
  margin-top: 100rem;
}

a.apply_title {
  color: #251714;
  font-family: HelveticaNeueLTProEx;
  font-size: 50rem;
  line-height: 1;
  margin-top: 100rem;
  text-decoration: none;
}

div.apply_line {
  background: #efcd24;
  width: 160rem;
  height: 8rem;
}

img.finger {
  margin-top: 200rem;
  position: relative;
  width: 300rem;
  left: 50%;
  transform: translate(-50%, 0);
}

/* white-space:pre-line; */
</style>

<script>
import global from "../global";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer"
import Footer from "../../components/footer/Footer"

//export default
export default {
  name: "Contact",
  data() {
    return {
      global: global,
    };
  },
  components: {
    Menu,
    LangTransfer,
    Footer
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    this.refresh_root_size();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
  },
  destroyed() {
  },
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  methods: {
    refresh_root_size() {
      let static_piexl = 700;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
  },
};
</script>



